.card-title {
    background-color: #eeeeee;
    border-radius: 10px 10px 0 0;
    padding: 5px 20px;
    min-height: 120px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}

.card-title h2 {
    color: #5c80bc;
    text-transform: lowercase;
    font-weight: 100;
}

.card-title h2::first-letter {
    text-transform: uppercase;
}


